.program-selection {
  @media (max-width: 768px) {
    border-top: 1px solid #d9d9d9;
    padding: 25px 20px 32px;
  }

  @media (min-width: 769px) {
    padding: 0 70px 72px;
  }
}

.program-selection__header {
  font-family: "Oswald", sans-serif;
  font-size: 36px;
  line-height: 36px;
  font-weight: 400;

  margin: 0 0 37px 0;

  @media (min-width: 769px) {
    font-size: 60px;
    line-height: 60px;
    margin-bottom: 54px;
  }
}

.program-selection__text {
  font-family: "Oswald", sans-serif;
  text-transform: uppercase;

  margin-bottom: 39px;

  @media (min-width: 769px) {
    font-size: 20px;
    line-height: 30px;
    width: 323px;
    margin-bottom: 66px;
  }
}
